export default {
  head() {
    return {
      title: 'Add Area'
    }
  },
  props: {
    prevData: {}
  },
  data() {
    return {
      name: '',
      sequence: '',
      nameRules: [
        v => !!v || 'Name is required',
      ],
      sequenceRules: [
        v => !!v || 'Sequence is required',
        v => Number.isInteger(Number(v)) || 'The value must be an integer number'
      ]
    };
  },
  methods: {
    backArea() {
      this.$router.push('/05');
      this.$emit('pop', this.prevData);
    },
    addNew() {
      this.$axios.$post('/areas', { sequence: this.sequence, name: this.name })
        .then(() => 
        {
          this.backArea();
        }).catch(error => 
        {
          console.log(error);
        });
    }
  }
}
